			}
		},
		mounted: function(){
			let _this=this;
			 //        nav
		        $('.check-head .box .my-tabs-nav li').click(function () {
		            var num = $(this).index();
		            $(this).addClass('active').siblings('li').removeClass('active');
		            $('#doc-my-tabs .my-tabs-bd .my-tab-panel:nth-child(' + (num + 1) + ')').removeClass('hidden').siblings('.my-tab-panel').addClass('hidden');
		        });

		        //cont-page的nav
		        $(' .check-form .my-tabs .my-tabs-bd .my-inner-nav li a').click(function () {
		            var id=$(this).attr('title');
		            $(id).removeClass('hidden').siblings('.cont-page').addClass('hidden');
		        });

		//            上传文档
		        function fileVerificat(file){
		            if (file.indexOf("txt") != -1||file.indexOf("docx") != -1||file.indexOf("doc") != -1||file.indexOf("pdf") != -1){
		                return true;
		            } else {
		                return false;
		            }
		        }
		        $(".form-upload").each(function(){
		            var fileNames = $(this).find('#fileName');
		            $(this).find('.upload-up-btn').on("change","#paperFile",function(){
		                var filePath=$(this).val();
		                if(fileVerificat(filePath)){
		                    var arr=filePath.split('\\');
		                    var fileName=arr[arr.length-1];
		                    fileNames.val(filePath);
		                }else{
		                    fileNames.val("您未上传文件，或者上传文件类型有误").show();
		                    return false
		                }
		            });
		        })

		//        校验是否登录该页面
		        function checkLogin() {
		            function GetRequest() {
		                var url = location.search; //获取url中"?"符后的字串
		                var theRequest = new Object();
		                if (url.indexOf("?") != -1) {
		                    var str = url.substr(1);
		                    strs = str.split("&");
		                    for (var i = 0; i < strs.length; i++) {
		                        theRequest[strs[i].split("=")[0]] = decodeURIComponent(strs[i].split("=")[1]);
		                    }
		                }
		                return theRequest;
		            }
		            var a = GetRequest();
		            var bb=false;
		            if(a['username']){
		                console.log(a['username']);
		                bb=true;

		            }else {
		                alert('哇哦！你还没有登录呦！请点击右上角进行登录或注册');
		            }

		            return bb;
		        }
		        //        拖拽事件
	            function drag() {
	                $('.dragitem').draggable({
	                    revert:true,
	                    proxy:'clone',
	                    onStartDrag:function(){
	                        $(this).draggable('options').cursor = 'not-allowed';
	                        $(this).draggable('proxy').css('z-index',999);
	                    },
	                    onStopDrag:function(){
	                        $(this).draggable('options').cursor='move';
	                    }
	                });


	                $('.cart').droppable({
	                    onDragEnter:function(e,source){
	                        $(source).draggable('options').cursor='auto';
	                    },
	                    onDragLeave:function(e,source){
	                        $(source).draggable('options').cursor='not-allowed';
	                    },
	                    onDrop:function(e,source){
	                        var name = $(source).html();
	                        addItem(name);
	                        $(source).css('display','none');
	                        // $(source).empty();

	                    }
	                });

	                $('.cart2').droppable({
	                    onDragEnter:function(e,source){
	                        $(source).draggable('options').cursor='auto';
	                    },
	                    onDragLeave:function(e,source){
	                        $(source).draggable('options').cursor='not-allowed';
	                    },
	                    onDrop:function(e,source){
	                        var name = $(source).html();
	                        addItem2(name);
	                        $(source).css('display','none');

	                    }
	                });
	                $('.cart3').droppable({
	                    onDragEnter:function(e,source){
	                        $(source).draggable('options').cursor='auto';
	                    },
	                    onDragLeave:function(e,source){
	                        $(source).draggable('options').cursor='not-allowed';
	                    },
	                    onDrop:function(e,source){
	                        var name = $(source).html();
	                        addItem3(name);
	                        $(source).css('display','none');

	                    }
	                });
	                function addItem(name) {
	                    var text=$('.cart').val();
	                    $('.cart').val(text+name+',')
	                }
	                function addItem2(name) {
	                    var text=$('.cart2').val();
	                    $('.cart2').val(text+name+',')
	                }
	                function addItem3(name) {
	                    var text=$('.cart3').val();
	                    $('.cart3').val(text+name+',')
	                }
	            }

	            $('.fc-box .form .submit').click(function () {
	                var reg=new RegExp("，","g");
	                var word2=$(".fc-box .form .tych").val()?$(".fc-box .form .tych").val():'';
	                var word1=$(".fc-box .form .tjch").val()?$(".fc-box .form .tjch").val():'';
	                var word3=$(".fc-box .form .fgbz").val()?$(".fc-box .form .fgbz").val():'';
	                if(word1||word2){
	                	word2=word2?word2:',';
	                	word1=word1?word1:',';
	                	word3=word3?word3:',';
	                    word1=word1.replace(reg,",");
	                    word2=word2.replace(reg,",");
	                    word3=word3.replace(reg,",");
	                    if(word2&&word2.indexOf(",")<0){
	                    	word2=word2+',';
	                    }else if(word1&&word1.indexOf(",")<0){
	                    	word1=word1+',';
	                    }else if(word3&&word3.indexOf(",")<0){
	                    	word3=word3+',';
	                    }
	                    console.log(word3);
	                    $.ajax({
	                        url: "http://121.42.29.188:10000/zhuajiacihui_cihuicunchu",
	                        type: 'post',
	                        dataType: "json",
	                        data: {
	                        	"username":_this.userName,
	                        	"qiye_id":'cihui',
	                        	"feilei_id":'cihui',
	                        	"yidian_name":'cihui_yaosu',
	                            "word1": word1,
	                            "word2": word2
	                        },
	                        success: function (result) {
	                            console.log(result);
	                            alert('提交成功');
	                            $(".fc-box .form .tych").val('');
	                            $(".fc-box .form .tjch").val('');

	                        }
	                    })
	                }else{
	                    alert('请填写要提交的停用词汇或者目标词汇');
	                }
                    
	            })
		        function getFileText(){
		            $('.fc-box ul').empty();
		            $('.cont-page .warp .tag ul').empty();
		            $('.cont-page .warp .marked ul').empty();
		            $(".fc-box .form .tjch").val('');
		            $(".fc-box .form .tych").val('');
		            var files=document.getElementById("paperFile").files[0];
		            if(files){
		                //                            显示第二步隐藏第一步
		                $('.xinxi').html('');
		                $('.c-contain').removeClass('hidden');
		                $('.check-head .box .my-tabs-nav li:nth-child(2)').addClass('active').siblings('li').removeClass('active');
		                $('#doc-my-tabs .my-tabs-bd .my-tab-panel:nth-child(2)').removeClass('hidden').siblings('.my-tab-panel').addClass('hidden');


		                var reader = new FileReader();//new一个FileReader实例
		                //判断文件类型，是不是text类型
		                if (/text+/.test(files.type)||/application+/.test(files.type)) {
		                    //                            显示第一步隐藏第二步
		                    $('.c-contain').removeClass('hidden');
		                    if(/application+/.test(files.type)){
		                        reader.readAsText(files, "gb2312");
		                    }else{
		                        reader.readAsText(files, "gb2312");
		                    }

		                    reader.onload = function() {
		                        console.log(this.result.toString());

		                        var filesText=this.result;

		                        cctitle(filesText);

		                    }
		                }else{
		                    alert('请上传文档');
		                }
		            }else{
		                alert('请上传文档');
		            }

		            function cctitle(filesText){
		                var colors=['#0080ff','#ffd9ec','#84c1ff','#c1ffe4','#ffffce',
		                    '#ffe6d9','#d9b3b3','#cdcd9a','#ffbfff','#a3d1d1','#b8b8dc','#9f35ff','#ff8000','#bbffbb',
		                    '#deffac','#ca8eff','#aaaaff','#ffb5b5','#97cbff','#e2c2de','#bebebe','#ff0000','#ffff00',
		                    '#fffacd','#ffe4e1','#ffdead','#ffb6c1','#ffa500','#ffa07a','#ff6347','#d2b48c','#ba55d3',
		                    '#bdb76b','#e0ffff','#ff4500','#fafad2','#f4a460','#f0e68c','#eee8aa','#d3d3d3','#afeeee',
		                    '#a9a9a9','#9370db','#87cefa','#7cfc00','#6b8e23','#66cdaa','#4169e1','#00bfff','','','',
		                    '','','','','','','','','','','','','','','','','','','','','','','','','','','','','',''];
		                $.ajax({
		                    url: "http://121.42.29.188:10000/zhuajiacihui_fenci",
		                    type: 'post',
		                    dataType: "json",
		                    data: {
		                        "text": filesText,
		                    },
		                    success: function (result) {
		                        console.log(result.data);
		                        for(var i=0;i<result.data.length;i++){
		                            if(result.data[i].value>5){
		                                $('.fc-box ul').append('<li class="dragitem cf-word color5">'+result.data[i].name+'</li>');
		                            }else if(result.data[i].value<=0){
		                                $('.fc-box ul').append('<li class="dragitem cf-word color0">'+result.data[i].name+'</li>');
		                            }else{
		                                $('.fc-box ul').append('<li class="dragitem cf-word color'+(result.data[i].value-1)+'">'+result.data[i].name+'</li>')
		                            }

		                        }
		                        drag();                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                       
		                    }
		                });
		                $.ajax({
		                    url: "http://121.42.29.188:10000/pingtai_cixingbiaozhu",
		                    type: 'post',
		                    dataType: "json",
		                    data: {
		                        "text": filesText,
		                    },
		                    success: function (result) {
		                        console.log(result.data);
		                        console.log(result.type);
		                        for(var a=0;a<result.data.length;a++){
		                            $('.marked ul').append('<li class="'+result.data[a].cixing+'">'+result.data[a].name+'/'+result.data[a].cixing+'</li>');
		                        }
		                        for(var i=0;i<result.type.length;i++){
		                            $('.tag ul').append('<li class="'+result.type[i].cixing+'">'+result.type[i].name+'/'+result.type[i].cixing+'</li>');
		                            $('.'+result.type[i].cixing).css('background-color',colors[i]);
		                        }
		                    }
		                });
		            }


		            function str_ch(color) {
		                for(var i=0;i<color.length;i++){
		                    if(color[i]==""){
		                        $('.fc-box ul .color'+i).css('background-color',"transparent");
		                    }else{
		                        $('.fc-box ul .color'+i).css('background-color',color[i]);
		                    }

		                }
		            }

		            var n=100;
		            var a=9;
		            var _move=false;//移动标记
		            var _x,_y;//鼠标离控件左上角的相对位置
		            $(".drag").click(function(){
		                //alert("click");//点击（松开后触发）
		            }).mousedown(function(e){
		                _move=true;
		                _x=e.pageX-parseInt($(".drag").css("left"));
		                _y=e.pageY-parseInt($(".drag").css("top"));
		                $(".drag").fadeTo(20, 0.5);//点击后开始拖动并透明显示
		            });

		            var x;
		            $(document).mousemove(function(e){
		                if(_move){
		                    x=e.pageX-_x;//移动时根据鼠标位置计算控件左上角的绝对位置
		                    if(x<=n/2){
		                        x=0;
		                    }else if(x>=(6*n-n/2)){
		                        x=n*6-a;
		                    }

		                    $(".drag").css({top:-8,left:x});//控件新位置
		                }
		            }).mouseup(function(){
		                _move=false;
		                var color=["#e0e0e0", "#d1d1d1", "#99ccff", "#7fbfff", "#ff9933", "#ff7733"]
		                if(x<n/2){
		                    x=0-a;
		                    color=["","","", "", "", ""];
		                    console.log(color);
		                    str_ch(color)
		                }
		                else if(x>=n/2&&x<(2*n-n/2)){
		                    x=n-a;
		                    color=["", "", "", "", "", "#ff7733"];
		                    console.log(color);
		                    str_ch(color)
		                }else if(x>=(2*n-n/2)&&x<(3*n-n/2)){
		                    x=n*2-a;
		                    color=["", "", "", "", "#ff9933", "#ff7733"]
		                    console.log(color);
		                    str_ch(color)
		                }else if(x>=(3*n-n/2)&&x<(4*n-n/2)){
		                    x=n*3-a;
		                    color=["", "", "", "#7fbfff", "#ff9933", "#ff7733"]
		                    console.log(color);
		                    str_ch(color)
		                }else if(x>=(4*n-n/2)&&x<(5*n-n/2)){
		                    x=n*4-a;
		                    color=["", "", "#99ccff", "#7fbfff", "#ff9933", "#ff7733"]
		                    console.log(color);
		                    str_ch(color)

		                }else if(x>=(5*n-n/2)&&x<(6*n-n/2)){
		                    x=n*5-a;
		                    color=["", "#d1d1d1", "#99ccff", "#7fbfff", "#ff9933", "#ff7733"]
		                    console.log(color);
		                    str_ch(color)
		                }else if(x>=(6*n-n/2)){
		                    x=n*6-a;
		                    color=["#e0e0e0", "#d1d1d1", "#99ccff", "#7fbfff", "#ff9933", "#ff7733"]
		                    console.log(color);
		                    str_ch(color)
		                }
		                $(".drag").css({top:-8,left:x});//控件新位置
		                $(".drag").fadeTo("fast", 1);//松开鼠标后停止移动并恢复成不透明

		            });
		        }
		        function getciku(num,type){
		        	$('.fc-box ul').empty();
		            $('.cont-page .warp .tag ul').empty();
		            $('.cont-page .warp .marked ul').empty();
		            $(".fc-box .form .tjch").val('');
		            $(".fc-box .form .tych").val('');
		            //                            显示第二步隐藏第一步
	                $('.xinxi').html('');
	                $('.c-contain').removeClass('hidden');
	                $('.check-head .box .my-tabs-nav li:nth-child(2)').addClass('active').siblings('li').removeClass('active');
	                $('#doc-my-tabs .my-tabs-bd .my-tab-panel:nth-child(2)').removeClass('hidden').siblings('.my-tab-panel').addClass('hidden');

		            $.ajax({
	                    url: "http://121.42.29.188:10000/cishi_cihuishaixuan_tuisong",
	                    type: 'post',
	                    dataType: "json",
	                    data: {
	                        "num": num,
	                        "biaoshi":type
	                    },
	                    success: function (result) {
	                        console.log(result.data);
	                        for(var i=0;i<result.data.length;i++){
                                $('.fc-box ul').append('<li class="dragitem cf-word">'+result.data[i]+'</li>');
	                        }
	                        drag();
	                    }
	                });
		        }
		        $('#submit .img1').click(function () {
		        	if(_this.userName){
		        		_this.file=true;
			        	$('#cikuFindBtn').css('display','none');
			            getFileText();
		        	}else{
		        		alert("请先登录该系统");
		        	}
		        });
		        $('#submit .img2').click(function () {
		        	if(_this.userName){
		        		_this.file=false;
			        	$('#cikuFindBtn').css('display','block');
		        	if($('#one').hasClass('hidden')){
		        		$('#one').removeClass('hidden');
		        		$('#two').addClass('hidden')
		        	}
		            getciku(100,1);
		        	}else{
		        		alert("请先登录该系统");
		        	}
		        	
		            
		        });
		        $('#cikuFindBtn').click(function(){
			        _this.file=false;
			        $('#cikuFindBtn').css('display','block');
		        	if($('#one').hasClass('hidden')){
		        		$('#one').removeClass('hidden');
		        		$('#two').addClass('hidden')
		        	}
			        var type;
			        if(_this.form.type==='停用词汇'){
			        	type=2
			        }else{
			        	type=1
			        }
		            getciku(_this.form.num1,type);
			    })
		        

		}
	}
</script>
<style>
