	}
	.fenci .fc-box{
		overflow: hidden;
		/*width: 80%;
		margin: 0 auto;*/
		min-height: 500px;
		padding:2%;
		border: 1px solid #ccc;
		margin-bottom: 20px;
		box-sizing: border-box;
	}
	.fenci .fc-box ul{
