	}
	.fenci .fc-box .my-group .chBox{
	    width: 100%;
	    height: 140px;
	}

	
	/*此行类别CSS*/
	.fenci #two .warp li{
	    float: left;
	    font-size: 16px;
	    color: #000;
	    width: auto;
